.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    max-width: 600px;
    width: 100%;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    background-color: #f9f9f9;
}

.container > div {
    margin: 10px;
}

/* Стили для SuperSelect */
.select {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    width: 100%;
    max-width: 200px; /* Выставим максимальную ширину */
    transition: border-color 0.3s;
}

.select:focus {
    border-color: #007bff; /* Цвет рамки при фокусе */
    outline: none;
}

/* Стили для SuperRadio */
.label {
    display: flex;
    align-items: center;
    margin: 5px 0;
}

.radio {
    margin-right: 8px; /* Отступ между радио и текстом */
}

/* Стили для span в радиокнопках */
.span {
    font-size: 16px;
    cursor: pointer; /* Визуальный указатель, что элемент кликабельный */
}

.span:hover {
    color: #007bff; /* Цвет текста при наведении */
}