.header {
    display: flex;                        /* Используем flexbox для выравнивания */
    align-items: center;                 /* Выравнивание по вертикали */
    justify-content: space-between;      /* Пространство между элементами */
    padding: 10px 20px;                  /* Внутренние отступы */
    background-color: #282c34;          /* Цвет фона */
    color: white;                        /* Цвет текста */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Тень для заголовка */
    position: relative;                  /* Для обязательного позиционирования */
}

.burgerMenuIcon {
    width: 30px;                        /* Ширина иконки бургера */
    height: 30px;                       /* Высота иконки бургера */
    cursor: pointer;                    /* Курсор указателя при наведении */
    transition: transform 0.3s ease;   /* Плавный переход при наведении */
}

.burgerMenuIcon:hover {
    transform: scale(1.1);              /* Увеличение иконки при наведении */
}

h1 {
    font-size: 24px;                    /* Размер шрифта заголовка */
    margin: 0;                          /* Убираем отступы по умолчанию */
}