






.sidebar {
    position: fixed; /* Фиксированное положение */
    top: 0;
    left: -250px; /* Изначально скрыто */
    width: 250px; /* Ширина сайдбара */
    height: 100%; /* Полная высота */
    background-color: #343a40; /* Темный фон */
    color: white; /* Белый текст */
    transition: left 0.3s ease; /* Анимация открытия */
    z-index: 1000; /* Расположение выше других элементов */
}

.sidebar.open {
    left: 0; /* Сайтбар становится видимым */
}

.background {
    position: fixed; /* Фоновая затемняющая область */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5); /* Полупрозрачный черный фон */
    z-index: 999; /* Размещаем под сайдбаром */
}

.close {
    background: transparent; /* Прозрачный фон у кнопки */
    border: none; /* Убираем границы */
    cursor: pointer; /* Указываем, что это интерактивная кнопка */
    margin: 15px; /* Отступы вокруг кнопки */
}

.nav {
    padding: 20px; /* Отступы внутри навигационного элемента */
}

.nav a {
    display: block; /* Делает ссылки блочными элементами */
    color: white; /* Цвет текста */
    text-decoration: none; /* Убираем подчеркивание по умолчанию */
    margin: 10px 0; /* Отступы между ссылками */
    transition: color 0.3s; /* Плавное изменение цвета при наведении */
}

.nav a:hover {
    color: #beceda; /* Цвет текста при наведении */
}
.nav a:active {
    color: #0056b3; /* Цвет текста при нажатии (активное состояние) */
}
.nav a.active {
    text-decoration: underline; /* Добавить подчеркивание для активной ссылки */
    color: #0056b3; /* Цвет текста для активной ссылки */
}
.close img {
    width: 24px;
    height: 24px;
}