.App {
    --header_height: 60px;

    position: relative; /* Убедитесь, что body не имеет фиксированной позиции */

}

.hw {
    display: flex;
    flex-direction: column; /* Вертикальное расположение элементов внутри контейнера */
    height: 100%; /* Заполняет всю высоту страницы */
    justify-content: flex-end; /* Блоки будут прижаты к нижней части */
}

.hwTitle {
    font-family: 'Arial', sans-serif; /* Шрифт заголовка */
    font-size: 1.5em; /* Размер шрифта */
    color: #333; /* Цвет текста */
    text-align: left; /* Выравнивание по центру */
    margin: 20px 0; /* Отступы сверху и снизу */
    padding: 10px; /* Внутренние отступы */
    background-color: #f4f4f4; /* Цвет фона */
    border-radius: 8px; /* Закругленные углы */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Тень */
}
.messages-container {
    position: fixed; /* Фиксированное положение */
    bottom: 10px; /* Отступ от нижнего края */
    right: 10px; /* Отступ от правого края */
    max-width: 300px; /* Максимальная ширина контейнера */
    z-index: 1000; /* Поверх остальных элементов */
    display: flex; /* Стили для flex-контейнера */
    flex-direction: column; /* Вертикальное расположение сообщений */
}
